import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { ValidationService } from 'common/validationService/validation.service';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';

@Component({
  selector: 'app-gymmembership-payment',
  templateUrl: './gymmembership-payment.component.html',
  styleUrls: ['./gymmembership-payment.component.css']
})
export class GymmembershipPaymentComponent implements OnInit,HttpSuccesFailureResponse {
     packageList=[{"id":1,"name":"Bronz 3 Month","price":225},{"id":2,"name":"Bronz 1 Year","price":630},
     {"id":3,"name":"Silver 3 Month","price":285},{"id":4,"name":"Silver 1 Year","price":798},
     {"id":5,"name":"Gold 3 Month","price":345},{"id":6,"name":"Gold 1 Year","price":966},
     {"id":7,"name":"Platinum 3 Month","price":435},{"id":8,"name":"Platinum 1 Year","price":1218}]

     packageId ;
     selectedPackage;
     currencyCode = "KD";
     loginForm: FormGroup;


  constructor(private http_authUtil:HttpUserAuthUtilsService,
    private _activatedRoute: ActivatedRoute,private formBuilder: FormBuilder) { }


    ngOnInit() {
      this._activatedRoute.queryParams.subscribe(params => {
        if (params['packageId']) {
          this.packageId = params['packageId'];
          this.setSelectedPackage();
         }
      });

      this.loginForm = this.formBuilder.group({
        email: ['', [Validators.required, ValidationService.emailValidator]],
        name:  ['', Validators.required],
        phone: ['',Validators.required]
    });


    }

    setSelectedPackage(){
      this.packageList.forEach(element => {
        if(this.packageId==element.id){
          this.selectedPackage = element;
        }
      });
    }

    pay(){
      if(this.selectedPackage&&this.loginForm.valid){
        let request = {
          "email":this.loginForm.value.email, "userName": this.loginForm.value.name, "phone": this.loginForm.value.phone, "amount": this.selectedPackage.price, "planName": this.selectedPackage.name
      }
     this.http_authUtil.gymmembershipPayment(request,this,true);
      }else{
      this.markFormGroupTouched(this.loginForm)
      }
    }
 private markFormGroupTouched(formGroup: FormGroup) {
        try {
            (<any>Object).values(formGroup.controls).forEach(control => {
                control.markAsTouched();
                if (control.controls) {
                    control.controls.forEach(c => this.markFormGroupTouched(c));
                }
            });
        }
        catch (ex) { }
    }



  onSuccess(type: any, responsedata: any) {
    window.location.assign(responsedata.paymentUrl)

  }
  onFailure(type: any, response: any) {
  }

 

}



