import { CommonModule } from '@angular/common';
import { routingGym } from './gymmembership.routing';
import { CoreModule } from 'core/core.module';
import { GymmembershipPaymentComponent } from './gymmembership-payment.component';

@NgModule({
  imports: [
    routingGym,
    ReactiveFormsModule,
    FormsModule,
    CoreModule,
    CommonModule,
  ],
  declarations: [
    GymmembershipPaymentComponent
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]

})
export class GymMembershipModule { }



